import React from "react";
import { Breadcrumb } from "@vechaiui/react";
import PageContext from "../assets/PageContext";

const Nav = () => {
  return (
    <PageContext.Consumer>
      {(context) => (
        <div className="pt-6 pr-6 float-right">
          <Breadcrumb separator="-">
            <Breadcrumb.Item currentPage={context.currentPage === "Home"}>
              <Breadcrumb.Link
                onClick={() => {
                  context.setCurrentPage("Home");
                }}
                style={{ textDecoration: "none" }}
              >
                Home
              </Breadcrumb.Link>
            </Breadcrumb.Item>
            <Breadcrumb.Item currentPage={context.currentPage === "Contact"}>
              <Breadcrumb.Link
                onClick={() => {
                  context.setCurrentPage("Contact");
                }}
                style={{ textDecoration: "none" }}
              >
                Contact
              </Breadcrumb.Link>
            </Breadcrumb.Item>
          </Breadcrumb>
        </div>
      )}
    </PageContext.Consumer>
  );
};

export default Nav;
