import React from "react";
import { Button, Input, Textarea } from "@vechaiui/react";
import { useForm, ValidationError } from "@formspree/react";

const Contact = () => {
  const [submitState, handleSubmit] = useForm("mzbqbpvw");

  return (
    <div className="grid md:grid-cols-7 p-5 mt-6 ">
      <div className="md:col-span-2" />
      <div className="md:col-span-3 card p-5 ">
        <form onSubmit={handleSubmit}>
          <div className="grid grid-cols-2">
            <div className="col-span-1">
              <span>Name</span>
            </div>
            <div className="col-span-2">
              <Input className="input" style={{ backgroundColor: "#bd5aff" }} />
              {/* This does absolutely nothing because I have to pay for it but you have no clue */}
            </div>
            <div className="col-span-1 mt-5">
              <span>Email</span>
            </div>
            <div className="col-span-2">
              <Input
                id="email"
                type="exmail"
                name="email"
                className="input"
                style={{ backgroundColor: "#bd5aff" }}
              />
              <ValidationError
                prefix="Email"
                field="email"
                errors={submitState.errors}
              />
            </div>
            <div className="col-span-1 mt-5">
              <span>Message</span>
            </div>
            <div className="col-span-2">
              <Textarea
                name="message"
                id="message"
                className="input"
                style={{ backgroundColor: "#bd5aff" }}
              />
              <ValidationError
                prefix="Message"
                field="message"
                errors={submitState.errors}
              />
            </div>
            <div className="col-span-2 mt-5">
              {submitState.succeeded ? (
                <Button disabled color="primary" className="w-full">
                  Sent!
                </Button>
              ) : (
                <Button
                  type="submit"
                  color="primary"
                  disabled={submitState.submitting}
                  className="w-full"
                >
                  Contact me!
                </Button>
              )}
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Contact;
